<script>
import style from "./style/media.css";
import { isMobile, isTablet, isIOS, isAndroid } from "mobile-device-detect";

import slideBanner from "./lib/slide_banner";

import store from "./store";

function hreflangInit(t) {
  var tmpArray = [];
  var xDefault = {
    rel: 'alternate',
    hreflang: 'x-default',
    href: store.state.url + '/' + t.locale
  };
  tmpArray.push(xDefault);
  for (let x in store.state.langList) {
    var tmpObj = {
      rel: 'alternate',
      hreflang: x,
      href: store.state.url + '/' + x
    };
    tmpArray.push(tmpObj);
  }
  return tmpArray
}

let links = function (t) {
  let tmpLinks = [
    {
      'rel': 'canonical',
      'href': store.state.url + '/' + t.locale + '/'
    },
    {
      'rel': 'alternate',
      'media': 'only screen and (max-width: 640px)',
      'href': store.state.url + '/' + t.locale + '/'
    },
    {
      'rel': 'alternate',
      'media': 'handheld',
      'href': store.state.url + '/' + t.locale + '/'
    }
  ]
  return tmpLinks.concat(hreflangInit(t))
}


export default {
  name: 'app',
  metaInfo() {
    return {
      title: this.$i18n.t('title'), // set a title
      meta: [
        {
          'http-equiv': 'content-language',
          'content': this.$i18n.locale
        },
        {
          'property': 'og:title',
          'content': this.$i18n.t('title')
        },
        {
          'property': 'og:url',
          'content': store.state.url + '/' + this.$i18n.locale + '/'
        },
        {
          'property': 'og:image',
          'content': store.state.url + '/' + 'img_banner_vido.jpg?t=20200429'
        },
        {
          'property': 'og:image:width',
          'content': '600'
        },
        {
          'property': 'og:image:height',
          'content': '315'
        },
        {
          'property': 'og:type',
          'content': 'website'
        },
        {
          'property': 'og:site_name',
          'content': 'VIDO app'
        },
        {
          'name': 'twitter:card',
          'content': 'summary'
        },
        {
          'name': 'twitter:title',
          'content': this.$i18n.t('title')
        },
        {
          'name': 'twitter:description',
          'content': this.$i18n.t('description')
        },
        {
          'name': 'twitter:image',
          'content': store.state.url + '/' + 'img_banner_vido.jpg?t=20200429'
        },
        {
          'property': 'og:description',
          'content': this.$i18n.t('description')
        },
        {
          'name': 'description',
          'content': this.$i18n.t('description')
        },
        {
          'name': 'apple-mobile-web-app-status-bar-style',
          'content': 'black'
        },
        {
          'name': 'apple-mobile-web-app-capable',
          'content': 'yes'
        }
      ],
      link: links(this.$i18n)
    }
  },
  mounted() {
    if (isMobile || isTablet) {
      document.body.classList.add('mobile')
    }
    slideBanner({
      time: 10000,
      container: document.querySelector('#slide_banner'),
      isLoop: true
      // initCount: 2
    });
    var dolBtn = document.querySelectorAll('.dol_btn');
    for (var i = 0; i < dolBtn.length; i++) {
      dolBtn[i].addEventListener('click', function () {
        ga('send', 'event', 'Google Play', 'click', this.getAttribute('data-count'));
      })
    }
    document.querySelector('#dol_btn_footer').addEventListener('click', function () {
      ga('send', 'event', 'Google Play', 'click', 'footer');
    })
  },
  data() {
    return {
      langCurrent: store.state.langList[this.$i18n.locale],
      langList: store.state.langList,
      fadeLeft: (!isMobile && !isTablet) ? 'fade fade_in_left': '',
      fadeRight: (!isMobile && !isTablet) ? 'fade fade_in_right': ''
    };
  },
  methods: {
    langListTrigger: function (e) {
      var list = document.querySelector('#footer_lang_list')
      if (list.classList.contains('hide')) {
        list.classList.remove('hide')
      } else {
        list.classList.add('hide')
      }
    }
  }
};
</script>

